import './dip.less'
import { Link } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import ApiProxy from '../../model/ApiProxy'
import backIcon from '../../assets/img/back.svg'
import axios from 'axios'
import { bg, calculateTimeDifference, findMaxMinSumIndex, getAmountETHAndDeri, timeDateFormat } from '../../utils/utils'
import { useCallback } from 'react'
import useContract from '../../hooks/useContractLp'
import { BigNumber } from 'ethers'
import { graph_request_test } from '../../utils/graph_api'
import { gql } from 'graphql-request'
import { useWallet } from 'use-wallet'

function DipSeven({ lang }) {
  const wallet = useWallet();
  const [value, setValue] = useState()
  const [optionInfo, setOptionInfo] = useState([])
  const [isGetLtokenId, setIsGetLtokenId] = useState(false)
  const [sumPower, setSumPower] = useState(0)
  const [deadline, setDeadline] = useState(false)
  const [disabled, setDisabled] = useState(true)
  const [isOptioned, setIsOptioned] = useState(false)
  const [votingPower, setVotingPower] = useState(0)
  const [useVotingPower, setUseVotingPower] = useState(0)
  const [timeAgo, setTimeAgo] = useState("")
  const [timeOut, setTimeOut] = useState(1701684900000)
  const [ArbUniDeri, setArbUniDeri] = useState(0)
  const [isTimeOut, setIsTimeOut] = useState(false)
  const alert = useAlert();
  const NFTContract = useContract("IERC721", 42161)
  const [deriLtokenId, setDeriLtokenId] = useState(0)
  const BTokenContract = useContract("LpTokenId", 42161)
  const PairContract = useContract("PairAbi", 42161)
  const PoolContract = useContract("arbitrumLp", 42161)

  const PairAddress = "0x96C3966534b4377691e11e163A0409Ad01667343"
  const arbitrumDeriAddress = "0x21e60ee73f17ac0a411ae5d690f908c3ed66fe12"

  const selectOption = useCallback((value) => {
    if (!isOptioned) {
      let radioI = document.getElementById("I")
      let radioII = document.getElementById("II")
      let radioIII = document.getElementById("III")
      let radioIV = document.getElementById("IV")
      if (value === "1") {
        radioI.click()
      } else if (value === "2") {
        radioII.click()
      } else if (value === "3") {
        radioIII.click()
      } else if (value === "4") {
        radioIV.click()
      }
      setValue(value)
    }

  }, [isOptioned])
  const getUserVotingPower = useCallback(async () => {
    let res = await ApiProxy.request("getUserVotingPower", [wallet.account, deriLtokenId || 0])
    setVotingPower(res)
  }, [wallet.account, deriLtokenId])
  const getUserVotingResult = useCallback(async () => {
    let res = await ApiProxy.request("getUserVotingResult", [wallet.account, deriLtokenId || 0])
    console.log("getUserVotingResult", res)
    if (res.option !== "0") {
      setValue(res.option)
      setIsOptioned(true)
    } else {
      setIsOptioned(false)
    }
  }, [wallet.account, deriLtokenId])

  const getArbUniDeri = useCallback(async () => {
    let NFTNumber = await NFTContract.balanceOf(wallet.account)
    NFTNumber = BigNumber.from(NFTNumber._hex).toString()
    let tokenId = []
    for (let index = 0; index < NFTNumber; index++) {
      let id = await NFTContract.tokenOfOwnerByIndex(wallet.account, index)
      id = BigNumber.from(id._hex).toString()
      tokenId.push(id)
    }
    let deriTokenId = [];
    for (let index = 0; index < tokenId.length; index++) {
      let id = await PoolContract.getPositionInfo(tokenId[index])
      if (id[0].toLocaleLowerCase() === PairAddress.toLocaleLowerCase()) {
        deriTokenId.push(tokenId[index])
      }
    }
    let data = deriTokenId.map(async (item) => {
      let obj = {}
      let res = await BTokenContract.positions(item)
      obj = {
        tickLower: res.tickLower,
        tickUpper: res.tickUpper,
        liquidity: BigNumber.from(res.liquidity._hex).toString()
      }
      return obj
    })
    data = await Promise.all(data)
    let tickCurrent = await PairContract.slot0()
    tickCurrent = tickCurrent.tick
    let amount = data.map((item) => {
      let [total_amount0, total_amount1] = getAmountETHAndDeri(tickCurrent, item.tickLower, item.tickUpper, item.liquidity)
      return {
        deri: total_amount0 / 10 ** 18,
        eth: total_amount1 / 10 ** 18
      }
    })
    let deriSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.deri;
    }, 0)
    console.log("getArbUniDeri", deriSum)
    setArbUniDeri(deriSum)
  }, [BTokenContract, PairContract, NFTContract, wallet.account, PoolContract])

  const getVotingResult = async () => {
    let res = await axios.get("https://vote.deri.io/get_voting_powers")
    console.log("getVotingResult", JSON.parse(res.data[0].votes))
    if (res.data && res.data[0].votes) {
      let timeNow = new Date()
      let MinNow = timeNow.getMinutes()
      let hoverNow = timeNow.getHours()
      let stemp = res.data[0].timestamp * 1000
      let timeAgo = new Date(stemp)
      let MinAgo = timeAgo.getMinutes()
      let hoverAgo = timeAgo.getHours()
      let timeMin = MinNow - MinAgo
      let timeHover = hoverNow - hoverAgo
      if (timeMin >= 1 && timeHover <= 0) {
        setTimeAgo(timeMin)
      } else if (timeHover > 0) {
        let min = 60 - MinAgo;
        let minTime = min + Number(MinNow);
        setTimeAgo(minTime)
      } else if (timeMin < 1) {
        let text = 'less than 1'
        setTimeAgo(text)
      }
      res = JSON.parse(res.data[0].votes)
      res = res.map(item => {
        return bg(item).div(10 ** 18).toNumber()
      })
      let { max, min, sum, maxIndex } = findMaxMinSumIndex(res)
      setSumPower(sum)
      setOptionInfo(res)
    }
  }

  const getV4LtokenId = useCallback(async () => {
    let query = gql`
      query ($chainId: Int!, $account: String!) {
        lpStates(where: { chainId: $chainId, account: $account }) {
          account
          b0Amount
          bAmount
          bToken
          cumulativePnl
          cumulativePnlPerLiquidity
          cumulativeTime
          lTokenId
          lastCumulativePnlOnEngine
          lastCumulativeTimePerLiquidity
          liquidity
        }
      }
    `;
    let variables = {
      account: wallet.account,
      chainId: 42161,
    };
    let data = await graph_request_test(query, variables);
    console.log(data)
    if (data && data.lpStates.length > 0) {
      let deriLtokenId = data.lpStates.find(item => item.bToken.toLocaleLowerCase() === arbitrumDeriAddress.toLocaleLowerCase())
      if (deriLtokenId) {
        setDeriLtokenId(deriLtokenId.lTokenId)
      }
    }
    setIsGetLtokenId(true)
  }, [wallet.account])

  const getVotingInfo = async () => {
    let res = await ApiProxy.request("getVotingInfo")
    console.log("getVotingInfo", res)
    if (res && res.length) {
      let time = res.find(i => +i.chainId === +wallet.chainId)
      let defaultTime = res.reduce((maxObj, currentObj) => {
        if (currentObj.deadline > maxObj.deadline) {
          return currentObj;
        }
        return maxObj;
      }, res[0]);
      time = time ? time.deadline + "000" : defaultTime.deadline + "000"
      let timestamp = new Date()
      setTimeOut(+time)
      if (timestamp.getTime() >= +time) {
        setDeadline(true)
        setIsTimeOut(true)
      } else {
        setIsTimeOut(false)
        setDeadline(false)
      }
    }

  }

  const vote = async () => {
    console.log([wallet.chainId, wallet.account, value])
    let res = await ApiProxy.request("vote", [wallet.chainId, wallet.account, value], { includeResponse: true })
    if (res.success) {
      getUserVotingPower()
      getUserVotingResult()
      getVotingResult()
      alert.success("Success")
    } else {
      alert.error("Failed")
    }

  }
  useEffect(() => {
    if (wallet.isConnected() && isGetLtokenId) {
      // getUserVotingPower()
      // getUserVotingResult()
      // getVotingInfo()
    }
  }, [wallet, deriLtokenId, isGetLtokenId])
  useEffect(() => {
    // getVotingResult()
  }, [])
  useEffect(() => {
    if (wallet.isConnected() && value && !isOptioned && !deadline) {
      setDisabled(false)
    } else {
      setDisabled(true)
    }
  }, [wallet, value, optionInfo, isOptioned])

  useEffect(() => {
    let sum = 0
    if (ArbUniDeri && votingPower) {
      sum = bg(ArbUniDeri).plus(votingPower).toNumber()
    } else if (votingPower) {
      sum = votingPower
    } else if (ArbUniDeri) {
      sum = ArbUniDeri
    }
    setUseVotingPower(sum)
  }, [ArbUniDeri, votingPower])

  useEffect(() => {
    if (wallet.account) {
      // getV4LtokenId()
    }
  }, [wallet.account])

  useEffect(() => {
    if (NFTContract && wallet.account && PoolContract && BTokenContract && PairContract) {
      // getArbUniDeri()
    }
  }, [NFTContract, wallet.account, PoolContract, BTokenContract, PairContract])
  return (
    <div className='dip-box'>
      <div className='back-governance'>
        <Link to='/governance'>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
        <div className='poll-detail'>
          {lang['poll-detail']}
        </div>
      </div>
      <div className='dip-box-info'>
        <div className='poll-dip-info '>
          <div className='dip-list-title'>
            <div className='title-text'>
              {lang['dip']}-009
            </div>

            <div className='dip-progress'>
              {/* <div className='progress-bg'></div>
              PROGRESSING */}
              <div className='executed-bg'></div> FINISHED
            </div>
          </div>
          <div className='dip-list-des '>
            Vote for Deri V4’s Fourth  i-Chain
          </div>
          <div className='dip-time'>
            {/* {timeOut ?
              isTimeOut ? calculateTimeDifference(timeOut) : <>
                {calculateTimeDifference(timeOut)}
                {" "}(Voting deadline: 2023 Dec 4th 10:00:00AM UTC)
              </> : "Voting deadline: 2023 Dec 4th 10:00:00AM UTC"} */}
            Target End Time: 2023 Dec 4th 10:00:00AM UTC (subject to extension, see below for details)
          </div>
        </div>
        <div className='dip-option'>
          <div className={value === "1" ? "option-check option-checked" : "option-check"} onClick={(e) => selectOption("1")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                  onChange={event => selectOption("1")}
                  checked={value === "1"}
                />
                <label for="I"></label>
                <span className='option-title-text'>
                  Base
                </span>
              </div>
              <div className='option-num'>
                1706194   {lang['deri-voting']} (32%)
                {/* {optionInfo[0] ? optionInfo[0].toFixed(0) : 0} {lang['deri-voting']} ({sumPower && optionInfo[0] ? ((optionInfo[0] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            {/* <div className='option-peng-bg' style={{ width: `${sumPower && optionInfo[0] ? ((optionInfo[0] / sumPower) * 100).toFixed(0) : 0}%` }}></div> */}
            <div className='option-peng-bg' style={{ width: `32%` }}></div>
          </div>
          <div className={value === "2" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("2")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                  onChange={event => selectOption("2")}
                  checked={value === "2"}
                />
                <label for="II"></label>
                <span className='option-title-text'>
                  BNBChain
                </span>
              </div>
              <div className='option-num'>
                1344869 {lang['deri-voting']} (25%)
                {/* {optionInfo[1] ? optionInfo[1].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[1] ? ((optionInfo[1] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            {/* <div className='option-peng-bg ' style={{ width: `${sumPower && optionInfo[1] ? ((optionInfo[1] / sumPower) * 100).toFixed(0) : 0}%` }}></div> */}
            <div className='option-peng-bg ' style={{ width: `25%` }}></div>
          </div>
          <div className={value === "3" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("3")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='3'
                  id="III"
                  onChange={event => selectOption("3")}
                  checked={value === "3"}
                />
                <label for="III"></label>
                <span className='option-title-text'>
                  Mantle
                </span>
              </div>
              <div className='option-num'>
                6534  {lang['deri-voting']} (0%)
                {/* {optionInfo[2] ? optionInfo[2].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[2] ? ((optionInfo[2] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            {/* <div className='option-peng-bg ' style={{ width: `${sumPower && optionInfo[2] ? ((optionInfo[2] / sumPower) * 100).toFixed(0) : 0}%` }}></div> */}
            <div className='option-peng-bg ' style={{ width: `0%` }}></div>
          </div>
          <div className={value === "4" ? "option-check option-checked" : "option-check"} onClick={() => selectOption("4")} >
            <div className='dip-one-option-title option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='4'
                  id="IV"
                  onChange={event => selectOption("4")}
                  checked={value === "4"}
                />
                <label for="IV"></label>
                <span className='option-title-text'>
                  Scroll
                </span>
              </div>
              <div className='option-num'>
                2273245 {lang['deri-voting']} (43%)
                {/* {optionInfo[3] ? optionInfo[3].toFixed(0) : 0}  {lang['deri-voting']} ({sumPower && optionInfo[3] ? ((optionInfo[3] / sumPower) * 100).toFixed(0) : 0}%) */}
              </div>
            </div>
            {/* <div className='option-peng-bg ' style={{ width: `${sumPower && optionInfo[3] ? ((optionInfo[3] / sumPower) * 100).toFixed(0) : 0}%` }}></div> */}
            <div className='option-peng-bg ' style={{ width: `43%` }}></div>
          </div>

          <div className='power-time'>
            {/* Last updated: {timeAgo} minutes ago */}
          </div>
          <div className='vote-button'>
            {/* <div className='your-power'>
              Your Voting Power :
              <DeriNumberFormat value={useVotingPower} decimalScale={2} /> DERI
            </div> */}
            {/* <Button disabled={disabled} label={isOptioned ? `${lang['vote']} Submitted` : lang['vote']} onClick={vote} width='286' fontSize='18' /> */}
            <Button disabled={true} label={lang['vote']} onClick={vote} width='286' fontSize='18' />
          </div>
        </div>
        <div className='dip-option-des-rules'>
          <div className='description-close-open'>
            {lang['description']}
          </div>
          <div className='description-text'>
            <div className='ul-description'>
              The “i-Chain”, one of the two main components of Deri V4, represents the requesting interface and serves as a pivotal entry point for user interactions. It adeptly handles all the user requests, from traders initiating orders and managing margins to liquidity providers adding or removing liquidity. While theoretically any layer 1 or layer 2 can be adopted as an i-Chain, in practice, the community will choose which ones to deploy.
            </div>
          </div>

          <div className='vote-rules'>
            <div className='rules-title'>
              {lang['voting-rules']}
            </div>
            <div className='rules-text'>
              <div className='rules-margin'>
                Your voting power is the summation of the following balances in your wallet as of the deadline:
                <ul className='no-margin'>
                  <li>DERI balance in your wallet</li>
                  <li>DERI balance in your LP tokens on Deri Pools</li>
                  <li>DERI amount contained in your DERI-USDT SLP tokens on SushiSwap</li>
                  <li>DERI amount contained in your DERI-BUSD CLP tokens on PancakeSwap</li>
                  <li>DERI amount contained in your DERI-WETH LP tokens on Uniswap V3</li>
                </ul>
              </div>
              <div className='rules-margin'>
                You can choose any of the 5 chains (ETH/Arbitrum/BNBChain/Linea/zkSync) to sign your vote by the deadline
              </div>
              <div className='rules-margin'>
                Voting Deadline = 2023 Dec 4th 10:00:00AM UTC
              </div>
              <div className='rules-margin'>
                When a vote is submitted less than 15 min to the deadline, the deadline will be extended by 15 min. That is, the voting period will continue to extend until there is no vote within the last 15 min.
              </div>
              <div className='rules-margin'>
                A snapshot will be taken at the block, at the deadline, and used to calculate the result.
              </div>
              <div className='rules-margin'>
                Please note that, if you had voted before the deadline but then had transferred your DERI, DLT, SushiSwap-LP, Pancake-LP, or Uniswap-LP before the deadline, it would be the balance at the deadline that counts
              </div>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Reference Poll Link
            </div>
            <div className='notes-text'>
              <a rel="noreferrer" target="_blank" href="https://snapshot.org/#/deri.eth/proposal/0x07cf364836a21430f1dcfb7e07794a9d167335176a702d17803400e5026bc142">Snapshot</a>
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              Discussion
            </div>
            <div className='notes-text'>
              <a rel="noreferrer" target="_blank" href="https://discord.com/invite/kb8ZbYgp8M">Discord</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet')(observer(DipSeven))


